  --color-green-400: #aad416;
  --color-green-800: #15732b;
  --color-red-100: #f45422;
  --color-black: #000;
  --rgba-black-250: rgba(0, 0, 0, 0.25);
  --rgba-black-700: rgba(0, 0, 0, 0.7);
  --gradient-primary: linear-gradient(360deg, #f3ffc5 0, #c8e992 100%);
  --font-default: "Geist";
  --font-title: "Unbounded";
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

ul,
ol {
  padding: 0;
}

