  display: inline-block;
  border: 0;
  padding: 0;
  background-color: transparent;
}

button {
  appearance: none;
  cursor: pointer;
}

input {
  margin: 0;
}

fieldset {
  margin: 0;
  border: 0;
  padding: 0;
}

legend {
  padding: 0;
}

picture {
  line-height: 0;
}

label {
  cursor: pointer;
}

.graph-modal {
  --transition-time: 0.3s;
  position: fixed;
  inset: 0;
  z-index: 1000;
  overflow: hidden auto;
  text-align: center;
  background-color: var(--rgba-black-700);
  opacity: 0;
  visibility: hidden;
  transition: opacity var(--transition-time), visibility var(--transition-time);
  cursor: pointer;
}

.graph-modal::before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.graph-modal__container {
  position: relative;
  display: inline-block;
  display: none;
  vertical-align: middle;
  margin: 50px auto;
  border-radius: 60px;
  padding: 65px 120px 65px 65px;
  width: 100%;
  max-width: 700px;
  text-align: left;
  background-color: var(--color-white);
  cursor: default;
}

.graph-modal__close {
  border-radius: 50%;
  width: 60px;
  height: 60px;
  background-color: var(--color-green-400);
  background-position: center;
  background-size: 22px;
  background-repeat: no-repeat;
  position: absolute;
  right: 26px;
  top: 26px;
  background-image: url("../img/icons/close.svg");
  transition: background-color 0.3s, box-shadow 0.3s;
}

.graph-modal__close:focus-visible {
  outline: 0;
  box-shadow: 0 0 0 2px var(--color-green-400);
}

.graph-modal__title {
  margin-bottom: 28px;
  font-family: var(--font-title), sans-serif;
  font-weight: 700;
  font-size: 28px;
  line-height: 1.3;
  color: var(--color-black);
  display: block;
}

.graph-modal__text {
  font-size: 24px;
}

.graph-modal__text p:not(:last-child) {
  margin-bottom: 12px;
}

.graph-modal__link {
  border-radius: 4px;
  transition: color 0.3s, box-shadow 0.3s;
}

.graph-modal__link:focus-visible {
  outline: 0;
  box-shadow: 0 0 0 2px var(--color-green-400);
}

.graph-modal.is-open {
  opacity: 1;
  visibility: visible;
  transition: opacity var(--transition-time), visibility var(--transition-time);
}

.graph-modal__container.graph-modal-open {
  display: inline-block;
}

.fade {
  opacity: 0;
}

.fade,
.fade.animate-open {
  transition: opacity var(--transition-time);
}

.fade.animate-open {
  opacity: 1;
}

.fadeInUp {
  opacity: 0;
  transform: translateY(-100px);
}

.fadeInUp,
.fadeInUp.animate-open {
  transition: transform var(--transition-time), opacity var(--transition-time);
}

.fadeInUp.animate-open {
  opacity: 1;
  transform: translateY(0);
}

