  right: 30px;
  top: unset;
  bottom: 26px;
  z-index: 2;
  color: var(--color-white);
  opacity: 1;
  transition: color 0.3s, background-color 0.3s;
}

.news-page .swiper-button-next::after,
.news-page .swiper-button-prev::after {
